/* @import url('https://fonts.googleapis.com/css?family=Baloo+Da+2&display=swap'); */

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  font-size: inherit;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  /* font-family: 'Baloo Da 2', cursive; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  font-size: 16px;
  color: #000;
  background: #fff;
}
body.nightmode {
  color: #fff;
  background: #000;
}
body.nightmode .MuiDrawer-paper,
body.nightmode .MuiPopover-paper {
  background: #444;
  color: #fff;
}
body.nightmode .MuiInputBase-root,
body.nightmode .MuiSelect-icon {
  color: #fff;
}
body.nightmode .MuiInput-underline:before {
  border-bottom: 1px solid rgba(255, 255, 255, 0.42);
}
body.nightmode .MuiSwitch-track {
  background-color: rgba(255, 255, 255, 0.72);
}
body.nightmode .page-dir-ico.left {
  background: linear-gradient(to right, #fff, transparent);
}
body.nightmode .page-dir-ico.right {
  background: linear-gradient(to left, #fff, transparent);
}
body.nightmode .page-dir-ico {
  opacity: .5;
}
body.nightmode .page-dir-ico svg {
  color: #fff;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.hidden {
  display: none !important;
}

#hiddenhelper {
  opacity: 0;
  z-index: -1;
}
.readerpage {
  position: fixed;
  left: 20px;
  right: 20px;
  top: 45px;
  white-space: pre-wrap;
  width: calc(100vw - 40px);
}
.nightmode .night-contrast {
  background-color: #fff;
  color: #000
}
